import {StringParamLanguage} from '../../../../../../@polygonjs/polygonjs/src/engine/params/utils/OptionsController';
import * as monaco from 'monaco-editor';
import {MonacoCommonOptions, monacoEditorCreateModel} from './MonacoEditorCommon';
interface SetupForJSONOptions {
	uniqueId: string;
}
export async function monacoEditorSetupForJSON(rawInput: string, element: HTMLElement,options:SetupForJSONOptions) {
	const editor = monaco.editor.create(element, {
		model: monacoEditorCreateModel(rawInput, StringParamLanguage.JSON,options.uniqueId),
		value: rawInput,
		language: StringParamLanguage.JSON,
		...MonacoCommonOptions,
	});
	return editor;
}
