import {StringParamLanguage} from '../../../../../../@polygonjs/polygonjs/src/engine/params/utils/OptionsController';
import * as monaco from 'monaco-editor';
import {MonacoCommonOptions, monacoEditorCreateModel} from './MonacoEditorCommon';
interface SetupForHTMLOptions {
	uniqueId: string;
}
export async function monacoEditorSetupForHTML(rawInput: string, element: HTMLElement, options: SetupForHTMLOptions) {
	const editor = monaco.editor.create(element, {
		model: monacoEditorCreateModel(rawInput, StringParamLanguage.HTML, options.uniqueId),
		value: rawInput,
		language: StringParamLanguage.HTML,
		...MonacoCommonOptions,
	});
	return editor;
}
